.PhonePricePanel {
  background-color: #f5f5f5;
  height: 100%;
  margin-top: 120px;
}

.PhonePricePanel div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.PhonePricePanel p {
  border-left: 2px solid #ccc;
  height: 90px;
  top: 8px;
  position: relative;
}
@media screen and (max-width: 630px) {
  .PhonePricePanel {
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 20px;
    background-color: transparent;
  }
  .PhonePricePanel p {
    display: none;
  }
}



